// Simple QR Code generator using canvas
const QRCodeCanvas = ({ value, size, fgColor, bgColor, shape }: any) => {
  const canvasRef = React.useRef<HTMLCanvasElement>(null)
  
  React.useEffect(() => {
    const canvas = canvasRef.current
    if (!canvas) return
    
    const ctx = canvas.getContext('2d')
    if (!ctx) return
    
    // Simple QR-like pattern for demo
    const cellSize = size / 25
    ctx.clearRect(0, 0, size, size)
    
    // Background
    ctx.fillStyle = bgColor
    ctx.fillRect(0, 0, size, size)
    
    // Draw QR pattern
    ctx.fillStyle = fgColor
    for (let i = 0; i < 25; i++) {
      for (let j = 0; j < 25; j++) {
        if (Math.random() > 0.5) {
          const x = i * cellSize
          const y = j * cellSize
          
          if (shape === 'rounded') {
            ctx.beginPath()
            ctx.arc(x + cellSize/2, y + cellSize/2, cellSize/2.5, 0, Math.PI * 2)
            ctx.fill()
          } else if (shape === 'circle') {
            ctx.beginPath()
            ctx.arc(x + cellSize/2, y + cellSize/2, cellSize/2, 0, Math.PI * 2)
            ctx.fill()
          } else {
            ctx.fillRect(x, y, cellSize * 0.9, cellSize * 0.9)
          }
        }
      }
    }
    
    // Draw positioning squares
    const drawPositioningSquare = (x: number, y: number) => {
      const sqSize = cellSize * 7
      ctx.fillStyle = bgColor
      ctx.fillRect(x, y, sqSize, sqSize)
      ctx.fillStyle = fgColor
      ctx.fillRect(x, y, sqSize, cellSize)
      ctx.fillRect(x, y, cellSize, sqSize)
      ctx.fillRect(x + sqSize - cellSize, y, cellSize, sqSize)
      ctx.fillRect(x, y + sqSize - cellSize, sqSize, cellSize)
      ctx.fillRect(x + cellSize * 2, y + cellSize * 2, cellSize * 3, cellSize * 3)
    }
    
    drawPositioningSquare(0, 0)
    drawPositioningSquare(size - cellSize * 7, 0)
    drawPositioningSquare(0, size - cellSize * 7)
  }, [value, size, fgColor, bgColor, shape])
  
  return <canvas ref={canvasRef} width={size} height={size} className="rounded-lg" />
}

